import React from 'react'
import SwitchMultiple from '../../atoms/SwitchMultiple/SwitchMultiple'
import TitleSimple from '../../atoms/TitleSimple/TitleSimple'
import LayoutGrafico from '../../molecules/LayoutGrafico/LayoutGrafico'
import imagen from '../../../img/banner_controles.png';
import "./UsuarioControlesDeposito.css";
import Button from '../../atoms/Button/Button';

export default function UsuarioControlesDeposito() {

    const text1 = 'Le permite limitar la cantidad de dinero que puede depositar en su cuenta a travs de Internet en un periodo de 24 horas, 7 das y/o 30 das.'
    const text2 = 'Dichas cantidades se podrn reducir en cualquier momento pero, si desea aumentarlas, este cambio se aplicar 24 despus de haber realizado la solicitud. Nuestro personal de Atencin al cliente puede proporcionarle ms informacin, sin embargo, no pueden sobrepasar los lmites establecidos por usted.'

    return (
        <div className="o-UsuarioMensajeNuevo" style={{gap:'5px'}}>

                <div className="o-UsuarioControlesDeposito-text">
                    <TitleSimple text={text1} bold={false} fontSize={13} align={'justify'}/>
                    <TitleSimple text={text2} bold={false} fontSize={13} align={'justify'}/>
                    <TitleSimple text={'Sus lmites de depsito existentes son los siguientes:'} bold={true} fontSize={15}/>
                    <div className="o-UsuarioControlesDeposito-text-column">
                        <TitleSimple text={'Lmite de depsito'} bold={false} fontSize={13}/>
                        <TitleSimple text={'Sin lmite de depsito establecido'} bold={true} fontSize={15}/>
                    </div>
                </div>
                <div className='o-UsuarioBancoDepositar-item'>
                    <Button text="Cambiar" type={1}/>
                </div>


        </div>

    )
}
