import {useContext, useState} from "react";
import {UserContext} from '../../../../src/context/UserProvider';

import Button from '../../atoms/Button/Button';
import Texto from '../../atoms/Texto/Texto';
import InputForm from '../../atoms/InputForm/InputForm';
import TitleSimple from '../../atoms/TitleSimple/TitleSimple';
export default function UsuarioMiCuentaContrasea() {
    const { user } = useContext(UserContext);

  return (
    <div className="o-UsuarioMensajeNuevo" style={{gap:'5px'}}>

            <TitleSimple text="Cambiar contrasea" bold={true} fontSize={15}/>
            <div className='o-UsuarioBancoDepositar-item' style={{paddingLeft:'15px'}}>
                <Texto text={'Por favor, utilice solo letras, nmeros y smbolos aceptados sin espacios, con un mnimo de 6 y un mximo de 32 caracteres.'} level={1}/>
                <Texto text={'Su contrasea no debe incluir su nombre o nombre de usuario, correo electrnico o ao de nacimiento. Puede aumentar la seguridad de su contrasea si utiliza una combinacin de letras, nmeros y smbolos. Recuerde que las contraseas distinguen entre mayculas y minsculas.'} level={1}/>
                <Texto text={'Podr utilizar los siguientes smbolos como parte de su contrasea:'} level={1}/>
                <Texto text={`! " # $ % & ' ( ) * + , - . / : ; < = > ? _ @ [ \ ] ^`+ '` { | } ~'} level={1}/>
            
            
            </div>

            <div className='o-UsuarioBancoDepositar-item'>
                <InputForm  placeholder="Contrasea actual"/>
            </div>
            <div className='o-UsuarioBancoDepositar-item'>
                <InputForm  placeholder="Contrasea nueva"/>
            </div>
            <div className='o-UsuarioBancoDepositar-item'>
                <Button text="Cancelar" type={1}/>
            </div>
    </div>
  )
}
